<template>
    <div class = "moviebox"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
    >
        <div class="loading" v-if="!movies"></div>
        <MovieItem
            v-else
            v-for = "movie in movies"
            :key = "movie.id"
            :movie = "movie"
        ></MovieItem>
    </div>
</template>

<script>
import MovieItem from "./MovieItem"
export default {
    props:["type"],
    watch:{
        type(){
            this.movies = [];
            this.page = 1;//
            this.hasMore = true;
            this.getMovies();
        }
    },
    data(){
        return{
            // movies:null,
            movies:[],//
            loading:false,
            limit:6,
            page:1,
            hasMore:true //
        }
    },
    //
    activated(){
        this.loading = false;//
    },
    //
    deactivated(){
        this.loading = true;//
    },
    methods:{
        loadMore(){
            // console.log("loadmore")
            if(!this.hasMore){
                //
                vant.Toast({
                    message: '',
                    position: 'bottom',
                    duration: 3000,
                })
                this.loading = true //false
                return false
            }
            this.getMovies();
        },
        getMovies(){
            //
            this.loading = true;
            /* let page = this.page;
            let limit = this.limit; */
            const toast1 = vant.Toast.loading({
                message: '',
                duration: 0,
            })
            // 
            let{page,limit} = this;
            this.$http.get("/api/db/" + this.type,{
                params:{
                    limit,
                    page
                }
                }).then(res=>{
                    // this.movies = res.data.object_list

                    //
                    this.movies = this.movies.concat(res.data.object_list)
                    //
                    this.loading = false;

                    // instance.close();//Toast

                    toast1.clear();
                    if(this.limit * this.page >= res.data.total){ //
                        this.hasMore = false //hasMorefalsefalsepage++
                        return false
                    }
                    this.page++
                })
        }
    },
    // created(){
    //     this.$http.get("/api/db/in_theaters",{
    //         params:{
    //             limit:6
    //         }
    //     }).then(res=>{
    //         this.movies = res.data.object_list
    //     })
    // },
    components:{
        MovieItem
    }
}   
</script>

<style>
    .moviebox{
        /* top:94px; */
        position:relative;
        /* margin-bottom:40px; */
    }
</style>
